.homepage-container {
    display: flex;
    align-items: left;
    justify-content: left;
    height: 100vh;
    background-color: #f2f2f2; /* Adjust background color as needed */
  }
  
  .homepage-content {
    text-align: left;
  }
  
  .homepage-title {
    font-size: 3em;
    font-weight: bold;
    color: #333; /* Adjust title color as needed */
    margin-bottom: 20px;
  }
  
  .homepage-description {
    font-size: 1.2em;
    color: #555; /* Adjust description color as needed */
    margin-bottom: 30px;
  }
  
  .homepage-button {
    background-color: #007bff; /* Adjust button color as needed */
    color: white;
    padding: 15px 30px;
    border: none;
    border-radius: 5px;
    font-size: 1.1em;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .homepage-button:hover {
    background-color: #0056b3; /* Adjust button hover color as needed */
  }
  
  .homepage-image {
    width: 200%;
    height: 500px;
    background: url('image.png') no-repeat center center;
    background-size: contain;
  }
  