.hardcore-container {
  font-family: Arial, sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #949494;
  padding: 20px;
  border-bottom: 2px solid #1a015f;
}

.header h1 {
  margin: 0;
  font-size: 24px;
}

.genre-selector {
  padding: 5px;
  font-size: 16px;
}

.bands-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 20px;
}

.band {
  width: 45%;
  margin-bottom: 20px;
}

.band h2 {
  margin-top: 0;
  font-size: 20px;
  color: #333;
}

.band p {
  font-size: 16px;
  color: #666;
}
